import { Component, OnInit, ViewChild, AfterViewInit, Input, NgZone, HostListener, ElementRef, Renderer2 } from '@angular/core';
import { ActivatedRoute, Router, NavigationStart } from '@angular/router';
import { Http, ResponseContentType } from '@angular/http';
import { DxFormComponent, DxPopoverComponent } from 'devextreme-angular';
import * as moment from 'moment';
import { Base64 } from 'js-base64';
declare var $: any;
import { Guid } from "guid-typescript";
import { ModifyModel, TemplateService } from '@modules/template/shared';
import { pattern } from '@config/index';
import { Library, AuthService, Helper, VStringService, sEnum } from '@core/index';
