  reloadFromServer = () => {
    let data = JSON.parse(JSON.stringify(this.frmData));
    let a = data.invoice_series.substr(0, 2);
    let b = data.invoice_series.substr(2, 2);
    let rgbColor = this.hexToRgb(data.json_data.watermark_text_color);
    if (rgbColor) {
      data.json_data.watermark_text_color = `${rgbColor.r};${rgbColor.g};${rgbColor.b}`;
    }
    data.invoice_series = a + '/' + b + 'E';
    data.template_code = `${this.invoice_type}0/` + data.template_code;
    if (this.isEdit()) {
      data.raw_file = this.frmData.raw_file;
      data.invoice_type = this.frmData.invoice_type;
    } else {
      data.raw_file = this.templateService.raw_file;
      data.invoice_type = this.templateService.invoice_type;
    }

    let temp = this.checkboxChuyenDoi ? 1 : 0;
    Library.showloading();
    this.templateService.getTemplateModify(data, temp).subscribe(rs => {
      if (rs.status == 'success') {
        this.dataPreview = Base64.decode(rs.data.template.html_file);
        // Helper.dowloadFile('demo.html', 'html', rs.data.template.html_file);
        this.genDatatoTemplate();
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      Library.hideloading();
    });
  }

  //// Thay đổi logo mẫu hóa đơn
  logoChanged(e) {
    let fileReader = new FileReader();
    let pattern = /image-*/;
    if (!e.target.files[0].type.match(pattern)) {
      Library.notify('Định dạng file không đúng', 'error');
      return;
    } else {
      if (e.target.files[0].size <= 307200) {
        fileReader.readAsDataURL(e.target.files[0]);
        let extension = e.target.files[0].name.split('.').pop();
        let image = new Image();
        image.src = window.URL.createObjectURL(e.target.files[0]);
        let maxWidth = 250;
        let maxHeight = 125;
        let maxSize = 150;
        // if (this.isTemVeThe) {
        //   if (this.paper_size == sEnum.paperSizes.A57Doc) {
        //     maxWidth = 99;
        //     maxHeight = 48;
        //     maxSize = 72;
        //   }
        //   if (this.paper_size == sEnum.paperSizes.A80Doc)
        //   {
        //     maxWidth = 181;
        //     maxHeight = 90;
        //     maxSize = 108;
        //   }
        // }
        fileReader.onload = (evt) => {
          image.onload = () => {
            let width = image.naturalWidth;
            let height = image.naturalHeight;
            let rateLogo = width / height;
            ///logo left
            let wlogo = $("#preview-iframe").contents().find(".logo-company-left").find(".logo-company").width();

            if (width >= height) {
              $("#preview-iframe").contents().find(".logo-company-left").find(".logo-company").css('height', 111 / rateLogo);
              $("#preview-iframe").contents().find(".logo-company-left").find(".logo-company").css('width', 111);
              if (rateLogo >= 2) {
                $("#preview-iframe").contents().find(".logo-company-left").find(".logo-company").resizable(
                  {
                    aspectRatio: true,
                    maxWidth: maxWidth,
                    maxHeight: maxHeight,
                  });
              }
              else {
                $("#preview-iframe").contents().find(".logo-company-left").find(".logo-company").resizable(
                  {
                    aspectRatio: true,
                    maxWidth: maxSize,
                    maxHeight: maxSize,
                  });
              }
            }
            else {
              $("#preview-iframe").contents().find(".logo-company-left").find(".logo-company").css('height', 150);
              $("#preview-iframe").contents().find(".logo-company-left").find(".logo-company").css('width', 150 * rateLogo);
              $("#preview-iframe").contents().find(".logo-company-left").find(".logo-company").resizable(
                {
                  aspectRatio: true,
                  maxWidth: maxSize * rateLogo,
                  maxHeight: maxSize,
                });
            }
            ///////////logo right
            if (width >= height) {
              $("#preview-iframe").contents().find(".logo-company-right").find(".logo-company").css('height', 111 / rateLogo);
              $("#preview-iframe").contents().find(".logo-company-right").find(".logo-company").css('width', 111);
              if (rateLogo >= 2) {
                $("#preview-iframe").contents().find(".logo-company-right").find(".logo-company").resizable(
                  {
                    aspectRatio: true,
                    maxWidth: maxWidth,
                    maxHeight: maxHeight,
                  });
              }
              else {
                $("#preview-iframe").contents().find(".logo-company-right").find(".logo-company").resizable(
                  {
                    aspectRatio: true,
                    maxWidth: maxSize,
                    maxHeight: maxSize,
                  });
              }
            }
            else {
              $("#preview-iframe").contents().find(".logo-company-right").find(".logo-company").css('height', 150);
              $("#preview-iframe").contents().find(".logo-company-right").find(".logo-company").css('width', 150 * rateLogo);
              $("#preview-iframe").contents().find(".logo-company-right").find(".logo-company").resizable(
                {
                  aspectRatio: true,
                  maxWidth: maxSize * rateLogo,
                  maxHeight: maxSize,
                });
            }
          };

          let base64result = fileReader.result.toString().split(',')[1];
          if (base64result) {
            $("#preview-iframe").contents().find(".logo-company-left").find(".logo-company").css('background-image', 'url(data:image/' + extension + ';base64,' + base64result + ')');
            $("#preview-iframe").contents().find(".logo-company-right").find(".logo-company").css('background-image', 'url(data:image/' + extension + ';base64,' + base64result + ')');
            $('.logo-file-label').html(e.target.files[0].name);
            this.frmData.json_data.logo_name = e.target.files[0].name;
          }
        }
      } else {
        Library.notify('Chỉ nên upload ảnh kích cỡ nhỏ hơn 300KB', 'error');
      }
    }
  }
