  ////// Thay đổi ảnh viền
  ImageBoxChanged(e) {
    this.items = [];
    this.listNameImage.forEach(item => {
      this.ObjectImage = {};
      let urlOrigion = environment.API_URL + "/image/khung/";
      let extended = ".jpg";
      this.ObjectImage.display_name = item.title;
      this.ObjectImage.id = Helper.makeid();
      let objectPaper = item.paperSize.filter(p => p == this.paper_size)[0]
      if ((item.paperSize && item.paperSize.length == 0) || objectPaper) {
        switch (this.paper_size) {
          case sEnum.paperSizes.A4Doc:
            this.ObjectImage.jpg_file = urlOrigion + item.name + extended;
            break;
          case sEnum.paperSizes.A4Ngang:
            this.ObjectImage.jpg_file = urlOrigion + item.name + "_" + sEnum.paperSizes.A4Ngang.toLowerCase() + extended;
            break;
          case sEnum.paperSizes.A5Ngang:
            this.ObjectImage.jpg_file = urlOrigion + item.name + "_" + sEnum.paperSizes.A5Ngang.toLowerCase() + extended;
            break;
          default:
            this.ObjectImage.jpg_file = urlOrigion + item.name + extended;
            break;
        }
        this.items.push(this.ObjectImage);
      }
    });
    $(".gallery").css('display', 'block');
    $(".inforCommon").css('display', 'none');
  }
