  styleFontInvoice() {
    let result = '';
    this.arrColumns.forEach(item => {
      let underline = $('#preview-iframe').contents().find('.' + item.value).css("text-decoration");
      let styleUnderLine = "none";
      if (underline)
        styleUnderLine = underline.split(" ");
      let color = $('#preview-iframe').contents().find('.' + item.value).css("color");
      let fontFamily = $('#preview-iframe').contents().find('.' + item.value).css("font-family");
      let fontSize = $('#preview-iframe').contents().find('.' + item.value).css("font-size");
      let fontWeight = $('#preview-iframe').contents().find('.' + item.value).css("font-weight");
      let fontStyle = $('#preview-iframe').contents().find('.' + item.value).css("font-style");
      let wordSpace = $('#preview-iframe').contents().find('.' + item.value).css("word-spacing");
      let display = $('#preview-iframe').contents().find('.' + item.value).css("display");
      let width = $('#preview-iframe').contents().find('.' + item.value).css("width");
      let textAlign = $('#preview-iframe').contents().find('.' + item.value).css("text-align");
      let important = "";
      if (display == "block")
        important = "!important"
      result += '.' + item.value + '{color:' + color + ';font-family:' + fontFamily + ';font-size:' + fontSize + ';font-weight:' + fontWeight + ';font-style:' + fontStyle + ';text-decoration:' + styleUnderLine[0] + ';word-spacing:' + wordSpace + ';display:' + display + ' ' + important + ';width:' + width + ';text-align:' + textAlign + '}';
    });
    return result;
  }
