  _validForm() {
    let _isvalid = true;
    let kyboi = "";
    let thongtin = this.dataSource.filter(p => p.code == "8e693984-576d-c322-7843-bdacf97ac189")[0];
    if (thongtin)
      kyboi = thongtin.value;
    let ngayky = moment(new Date()).format('DD/MM/YYYY  HH:mm:ss');
    // Ký bởi người bán
    if ($("#preview-iframe").contents().find(".seler-sign-name")[0])
      $("#preview-iframe").contents().find(".seler-sign-name").html(kyboi);
    // Ngày ký người bán
    if ($("#preview-iframe").contents().find(".seler-sign-date")[0])
      $("#preview-iframe").contents().find(".seler-sign-date").html(ngayky);
    let heightSeller = $("#preview-iframe").contents().find(".sign-block").find(".efy-esign-parent").innerHeight();
    let heightCKS = $("#preview-iframe").contents().find(".sign-block").find(".efy-esign-parent").find(".efy-esign-none").innerHeight();
    if (heightCKS > heightSeller) {
      Library.notify("Diện tích chữ ký số đang vượt quá giới hạn được thiết lập", "error");
      _isvalid = false;
      return false;
    }
    if (this.isBienLaiDienTu && this.frmData.template_type == sEnum.templatetype.BienLaiInSanMenhGia) {
      if (VStringService._getValueToString(this.fee_name) == "") {
        Library.notify("Tên loại phí, lệ phí không được để trống", "error");
        _isvalid = false;
        return false;
      }
      if (this.fee_money == null) {
        Library.notify("Số tiền không được để trống", "error");
        _isvalid = false;
        return false;
      }
    }
    return _isvalid;
  }

