  getBase64ImageFromURL(url: string, img) {
    let is_tem_ve = this.isTemVeThe;
    return Observable.create((observer: Observer<string>) => {
      // create an image object
      img.crossOrigin = 'Anonymous';
      img.src = url;
      if (!img.complete) {
        // This will call another method that will create image from url
        img.onload = () => {
          observer.next(this.getBase64Image(img, is_tem_ve));
          observer.complete();
        };
        img.onerror = (err) => {
          observer.error(err);
        };
      } else {
        observer.next(this.getBase64Image(img, is_tem_ve));
        observer.complete();
      }
    });
  }
  getBase64Image(img: HTMLImageElement, is_tem_ve) {
    // We create a HTML canvas object that will create a 2d image
    var canvas = document.createElement("canvas");
    canvas.width = img.width;
    canvas.height = img.height;
    var ctx = canvas.getContext("2d");
    // This will draw image    
    if (is_tem_ve)
      ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
    else
      ctx.drawImage(img, 0, 0);
    // Convert the drawn image to Data URL
    var dataURL = canvas.toDataURL("image/png");
    return dataURL.replace(/^data:image\/(png|jpg);base64,/, "");
  }


  imageToShow: any;

  createImageFromBlob(image: Blob) {
    let reader = new FileReader();
    reader.addEventListener("load", () => {
      return reader.result;
    }, false);

    if (image) {
      reader.readAsDataURL(image);
    }
  }
  getImageFromService(imageUrl: string) {
    this.getImage(imageUrl).subscribe(data => {
      return this.createImageFromBlob(data);
    }, error => {
    });
  }
  getImage(imageUrl: string): Observable<Blob> {
    return this.http.get(imageUrl, { responseType: 'blob' });
  }
  setStatusDataOnCellPrepared(e, message) {
    if (!e.value) {
      e.cellElement.style.background = "red url('assets/img/icons/fa-remove.png') no-repeat 2% center";
      e.cellElement.title = message;
      if (e.data.statusData != "ERROR_DUPLICATE_DATABASE" && e.data.statusData != "ERROR_DUPLICATE_ONGIRD") e.data.statusData = "DATA_ERROR";
    }
    else if (e.value && e.data.statusData == "DATA_ERROR") {
      e.data.statusData = "INVALID"
    }
  }
  onCellPrepared(e) {
    if (e.rowType == "data") {
      if (e.data.type == "2") {
        switch (e.column.dataField) {
          // case "name":
          //   if (e.data.name == "") this.setStatusDataOnCellPrepared(e, "Thông tin không được để trống");
          //   break;
          case "value":
            if (e.data.value == "") this.setStatusDataOnCellPrepared(e, "Thông tin không được để trống");
            break;
          default:
            break;
        }
      }
      if (e.data.type == "1") {
        switch (e.column.dataField) {
          case "name":
            if (e.data.name == "") this.setStatusDataOnCellPrepared(e, "Thông tin không được để trống");
            break;
          default:
            break;
        }
      }
    }
  }
