  createRowData(countRow, id) {
    for (let i = 0; i < countRow; i++) {
      this.trRow += '<tr class="detail-data font-color-data text-left">';
      var ths = $("#" + id).contents().find("#tbDetail").find("th");
      for (var j = 0; j < ths.length; j++) {
        let className = ths[j].className;
        let column = this.arrColumns.filter(p => p.name == className)[0];
        if (column) {
          let valueColumn = column.value;
          let height = column.height;
          if (this.paper_size == sEnum.paperSizes.A5Ngang)
            height = "18px";
          if (i > 0)
            valueColumn = '';
          if ($("#" + id).contents().find("." + column.name)[0])
            this.trRow += '<td style="height: ' + height + ';"' + ' class="' + column.class + '"><span class="' + valueColumn + '"></span></td>';
        }
      }
      this.trRow += '</tr>';
    }
  }
  SetFontVerticalAlign(align) {
    if (this.arrFontChange.length > 0) {
      for (let i = 0; i < this.arrFontChange.length; i++) {
        this.arrIds.forEach(id => {
          let column = this.arrColumns.filter(p => p.value == this.arrFontChange[i])[0];
          if (column)
            $("#" + id).contents().find("." + this.arrFontChange[i]).css('display', 'block');
          if (!this.arrFontChange[i].includes("name-"))
            $("#" + id).contents().find("." + this.arrFontChange[i]).css('width', '100%');
          $("#" + id).contents().find("." + this.arrFontChange[i]).css('text-align', align);
        });
      }
    }
  }
  onNoDisplayInvoiceViewer = (e) => {
    if (e.value) {
      $("#preview-iframe").contents().find('.invoice_date:parent').each(function () {
        $(this).insertBefore($(this).prev('.instance_invoice'));
      });
      $('#preview-iframe').contents().find(".ban-sao").html('&nbsp;&nbsp;&nbsp;&nbsp;');
      $('#preview-iframe').contents().find(".ban-sao-in-duoi").html('&nbsp;&nbsp;&nbsp;&nbsp;');
    } else {
      $("#preview-iframe").contents().find('.instance_invoice:parent').each(function () {
        $(this).insertBefore($(this).prev('.invoice_date'));
      });
      $('#preview-iframe').contents().find(".ban-sao").html('(Bản thể hiện của hóa đơn điện tử)');
      $('#preview-iframe').contents().find(".ban-sao-in-duoi").html('(E-Invoice viewer)');
    }
  }
  /* Thay đổi text */
  textChanged(e) {
    this.textAria = e.srcElement.value;
    if (this.arrFontChange.length > 0) {
      // if (e.previousValue) {
      for (let i = 0; i < this.arrFontChange.length; i++) {
        this.arrIds.forEach(id => {
          $("#" + id).contents().find("." + this.arrFontChange[i]).html(e.srcElement.value);
        });
        ///
        this.dataSourceBenMua.forEach(item => {
          let name = "name-" + item.code;
          let italic = "italic-" + item.code;
          if (name == this.arrFontChange[i] || italic == this.arrFontChange[i])
            item.name = e.srcElement.value;
        });
      }
      // }
    }
  }
