import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { IndexComponent } from './components/index/index.component';
import { InitComponent } from './components/init/init.component';
import { ModifyComponent } from './components/modify/modify.component';
import { ModifynewComponent } from './components/modifynew/modifynew.component';

import { TemplateDetailResolver, InitResolver } from './shared';

const custom_template_brec = [
  {
    label: {brec: "Mẫu hóa đơn"},
    url: "/system/template"
  },
  {
    label: {brec: "Chọn mẫu"},
    url: "/system/template/init"
  },
  {
    label: {brec: "Tạo và chỉnh sửa"},
    url: ""
  },
];

const routes: Routes = [
  { path: '', component: IndexComponent},
  { path: 'init', component: InitComponent, resolve: {init: InitResolver}, data: {brec: 'Chọn mẫu'}},
  { path: 'old', component: ModifyComponent,  resolve: {template: TemplateDetailResolver}, data: {
    brec: 'Tạo và chỉnh sửa',
    custom_brec: custom_template_brec
  }},
  { path: 'new', component: ModifynewComponent,  resolve: {template: TemplateDetailResolver}, data: {
    brec: 'Tạo và chỉnh sửa',
    custom_brec: custom_template_brec
  }},
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class routing {}
