import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { TemplateService } from '@modules/template/shared';
import { Library } from '@core/library';
import { AuthService, Helper, sEnum } from '@core/index';
declare var $: any;

@Component({
  selector: 'app-init',
  templateUrl: './init.component.html',
  styleUrls: ['./init.component.scss']
})
export class InitComponent implements OnInit, OnDestroy {
  @ViewChild('tabPanel') tabPanel: any;
  tabs = [];
  offsetEntity = 4;
  endLoad = false;
  templateTypes: any;
  invoiceTypes: any = [];
  templateTypesCurrent: any = [
    {
      name: "Mẫu cơ bản",
      code: "MAU_CO_BAN"
    },
    {
      name: "Thư xác nhận thu nhập",
      code: "THU_XAC_NHAN_THU_NHAP"
    },
    {
      name: "Hóa đơn máy tính tiền",
      code: sEnum.templatetype.HoaDonKhoiTaoTumayTinhTien
    },
    {
      name: "Phiếu tính tiền",
      code: 'PHIEU_THU_MAY_TINH_TIEN'
    },
    {
      name: "Hóa đơn bệnh viện",
      code: "HOA_DON_BENH_VIEN"
    },
    {
      name: "Hóa đơn điện nước",
      code: "HOA_DON_DIEN_NUOC"
    },
    {
      name: "Hóa đơn Logistic",
      code: "HOA_DON_LOGISTIC"
    },
    {
      name: "Hóa đơn thuế nhà thầu",
      code: "HOA_DON_THUE_NHA_THAU"
    },
    {
      name: "Hóa đơn vé máy bay",
      code: "HOA_DON_VE_MAY_BAY"
    },
    {
      name: "Hóa đơn khối trường học",
      code: "HOA_DON_KHOI_TRUONG_HOC"
    },
    {
      name: "Hóa đơn nhà hàng, khách sạn",
      code: "HOA_DON_NHA_HANG_KHACH_SAN"
    },
    {
      name: "Hóa đơn trung tâm đăng kiểm",
      code: "HOA_DON_DANG_KIEM"
    },
    {
      name: "Hóa đơn xăng dầu",
      code: "HOA_DON_XANG_DAU"
    },
    {
      name: "Hóa đơn bất động sản",
      code: "HOA_DON_BAT_DONG_SAN"
    },
    {
      name: "Hóa đơn kinh doanh xe",
      code: "HOA_DON_KINH_DOANH_XE"
    },
    {
      name: "Hóa đơn kiêm tờ khai hoàn thuế",
      code: "HOA_DON_KIEM_TO_KHAI_HOAN_THUE"
    },
    {
      name: "Khác",
      code: "KHAC"
    },
  ];
  vatTypes: any = [
    {
      name: "Mẫu một loại thuế",
      code: "MAU_MOT_LOAI_THUE"
    },
    {
      name: "Mẫu nhiều loại thuế",
      code: "MAU_NHIEU_LOAI_THUE"
    },
  ];
  languages: any = [
    {
      name: "Tiếng việt",
      code: "VI"
    },
    {
      name: "Song ngữ(Việt-Anh)",
      code: "EN"
    },
  ];
  papersizes: any = [
    {
      name: "A4 dọc",
      code: "A4_DOC"
    },
    {
      name: "A4 ngang",
      code: "A4_NGANG"
    },
    {
      name: "A5 ngang",
      code: "A5_NGANG"
    },
  ];
  typeCirculars: any = [
    {
      name: "Thông tư 32",
      code: "THONG_TU_32"
    },
    {
      name: "Thông tư 78",
      code: "THONG_TU_78"
    },
  ];
  billPaperSizes = [];
  receiptTypeArray = [];
  ds_loai_ve = [];
  subPaperSizes: any;
  invoice_type: string = "01GTKT";
  template_type: string = "MAU_CO_BAN";
  arrayTemplate: any = ["MAU_CO_BAN"];
  vat_type: string = "MAU_MOT_LOAI_THUE";
  language: string = "EN";
  papersize: string = "A4_DOC";
  type_circulars: string = "THONG_TU_78"
  is_multi_vat: boolean = false;
  vnArray = [];
  findData = {
    invoice_type: this.invoice_type,
    template_type: this.arrayTemplate,
    is_multi_vat: this.is_multi_vat,
    language: this.language,
    paper_size: this.papersize,
    type_circulars: this.type_circulars
  };
  _translate: any;
  constructor(
    private authService: AuthService,
    private route: ActivatedRoute,
    private router: Router,
    private templateService: TemplateService,
    public translate: TranslateService,
  ) {
    translate.use(this.authService.getLanguage());
    this._translate = this.authService.getTranslate();
  }

  ngOnInit() {
    this._loadDataDefault();
    this.templateTypes = this.templateTypesCurrent.filter(item => item.code !== "THU_XAC_NHAN_THU_NHAP")
    this.subPaperSizes = this.papersizes;
    this.route.data.subscribe((data: any) => {
      this.tabs[0] = {
        title: "Kho mẫu hóa đơn dùng chung",
        items: data.init
      };

      this.tabs[1] = {
        title: "Kho mẫu hóa đơn của đơn vị",
        items: []
      };
    });

    $('.main').scroll(() => {
      let a = $('.main').scrollTop() + $('.main').height() - $('.main')[0].scrollHeight;
      if (Math.round(a * 1000) / 1000 == 0) {
        if (!this.endLoad) {
          Library.showloading();
          let sub = this.templateService.getTemplate(this.findData, 3, this.offsetEntity).subscribe(rs => {
            if (rs.status == 'success') {
              if (rs.data.list_invoice_form.length == 0) {
                this.endLoad = true;
              }
              rs.data.list_invoice_form.forEach(item => {
                this.tabs[0].items.push(item);
              });
              this.offsetEntity += 3;
            } else {
              Library.notify(rs.message, 'error');
            }
          }, err => { }, () => {
            sub.unsubscribe();
            Library.hideloading();
          });
        }
      }
    });
  }

  selectTab(e) {
    if (e.addedItems[0].title == "Kho mẫu hóa đơn của đơn vị") {
      if (!this.tabs[1].items.length) {
        Library.showloading();
        this.templateService.getUnitTemplate(this.invoice_type).subscribe(rs => {
          if (rs.status == 'success') {
            this.tabs[1].items = rs.data.templates;
          } else {
            Library.notify(rs.message, 'error');
          }
        }, err => { }, () => {
          Library.hideloading();
        });
      }
    }
  }

  loadData(findData) {
    // Library.showloading();
    // this.templateService.getAllTemplate(invoice_type).subscribe(rs => {
    //   this.tabs[0] = {
    //     title: "Kho mẫu hóa đơn dùng chung",
    //     items: rs[0].data.list_invoice_form
    //   };

    //   this.tabs[1] = {
    //     title: "Kho mẫu hóa đơn của đơn vị",
    //     items: rs[1].data.templates
    //   };
    // }, err => {}, () => {
    //   Library.hideloading();
    // });
    Library.showloading();
    this.templateService.getTemplate(this.findData, 3, 1).subscribe(rs => {
      if (rs.status == 'success') {
        this.tabs[0].items = rs.data.list_invoice_form;
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      Library.hideloading();
    });
  }

  chosseTemplate(item) {
    if (item.json_data) {
      // this.router.navigate([`system/template/${item.id}`]);
      Library.showloading();
      this.templateService.show(item.id).subscribe(rs => {
        if (rs.status == 'success') {
          let data = rs.data.template;
          if (this.invoice_type == sEnum.loaihoadon.VETHEGTKT01 || this.invoice_type == sEnum.loaihoadon.VETHEGTTT02) {
            if (this.invoice_type == sEnum.loaihoadon.VETHEGTKT01)
              this.templateService.invoice_type = sEnum.loaihoadon.GTKT01;
            if (this.invoice_type == sEnum.loaihoadon.VETHEGTTT02)
              this.templateService.invoice_type = sEnum.loaihoadon.GTTT02;
          }
          else
            this.templateService.invoice_type = this.invoice_type;
          this.templateService.raw_file = data.raw_file;
          this.templateService.html_file = data.html_file;
          this.templateService.template_id = data.id;
          this.templateService.number_of_row = data.number_of_row;
          this.templateService.template_type = data.template_type;
          this.templateService.paper_size = data.paper_size;
          //trường thêm
          this.templateService.is_multi_vat = data.is_multi_vat;
          this.templateService.is_multi_pages = data.is_multi_pages;
          this.templateService.seller_sign_position = data.seller_sign_position;
          this.templateService.buyer_sign_position = data.buyer_sign_position;
          this.templateService.is_discount = data.is_discount;
          this.templateService.invoice_type_begin = data.release_info ? data.release_info.invoice_type : "";
          this.templateService.invoice_series_begin = data.release_info ? data.release_info.invoice_series : "";
          this.templateService.template_code_begin = data.release_info ? data.release_info.template_code : "";
          this.router.navigate(['system/template/create']);
        } else {
          Library.notify(rs.message, 'error');
        }
      }, err => { }, () => {
        Library.hideloading();
      });
    } else {
      Library.showloading();
      let tax_refund_form = false;
      if (item.template_type == "HOA_DON_KIEM_TO_KHAI_HOAN_THUE")
        tax_refund_form = true;
      let obj =
      {
        id: item.id,
        tax_refund_form: tax_refund_form
      }
      this.templateService.showShareTemplate(obj).subscribe(rs => {
        if (rs.status == 'success') {
          let data = rs.data.invoice_form;
          if (this.invoice_type == sEnum.loaihoadon.VETHEGTKT01 || this.invoice_type == sEnum.loaihoadon.VETHEGTTT02) {
            if (this.invoice_type == sEnum.loaihoadon.VETHEGTKT01)
              this.templateService.invoice_type = sEnum.loaihoadon.GTKT01;
            if (this.invoice_type == sEnum.loaihoadon.VETHEGTTT02)
              this.templateService.invoice_type = sEnum.loaihoadon.GTTT02;
          }
          else
            this.templateService.invoice_type = this.invoice_type;
          this.templateService.raw_file = data.raw_file;
          this.templateService.html_file = data.html_file;
          this.templateService.template_id = data.id;
          this.templateService.number_of_row = data.number_of_row;
          this.templateService.template_type = data.template_type;
          this.templateService.paper_size = data.paper_size;
          //trường thêm
          this.templateService.is_multi_vat = data.is_multi_vat;
          this.templateService.is_multi_pages = data.is_multi_pages;
          this.templateService.seller_sign_position = data.seller_sign_position;
          this.templateService.buyer_sign_position = data.buyer_sign_position;
          this.templateService.is_discount = data.is_discount;
          this.templateService.invoice_type_begin = data.release_info ? data.release_info.invoice_type : "";
          this.templateService.invoice_series_begin = data.release_info ? data.release_info.invoice_series : "";
          this.templateService.template_code_begin = data.release_info ? data.release_info.template_code : "";
          this.templateService.type_circular = data.type_circulars;
          if (tax_refund_form) {
            this.templateService.raw_file_attach = data.raw_file_temp;
            this.templateService.html_file_attach = data.raw_file_temp;
          }
          this.router.navigate(['system/template/new']);
          // this.router.navigate(['system/template/old']);
        } else {
          Library.notify(rs.message, 'error');
        }
      }, err => { }, () => {
        Library.hideloading();
      });
    }
  }

  invoiceTypeChange(e) {
    this.endLoad = false;
    this.offsetEntity = 4;
    this.invoice_type = e.value;
    this.papersize = "A4_DOC";
    this.templateTypes = this.templateTypesCurrent.filter(item => item.code !== "THU_XAC_NHAN_THU_NHAP");
    this.setLanguageByTypeInvoice();
    switch (this.invoice_type) {
      case sEnum.loaihoadon.CTT56:
        this.templateTypes = this.templateTypesCurrent.filter(item => item.code === "MAU_CO_BAN" || item.code === "THU_XAC_NHAN_THU_NHAP");
        break;
      case sEnum.loaihoadon.BLP:
        this.templateTypes = this.receiptTypeArray;
        break;
      case sEnum.loaihoadon.VETHEGTKT01:
      case sEnum.loaihoadon.VETHEGTTT02:
        this.templateTypes = this.ds_loai_ve;
        this.papersize = sEnum.paperSizes.A80Doc;
        break;
    }
    if (this.invoice_type == sEnum.loaihoadon.HSPT || this.invoice_type == sEnum.loaihoadon.BLP)
      this.papersize = "A5_NGANG";
    if (this.template_type != this.templateTypes[0].code)
      this.template_type = this.templateTypes[0].code;
    else {
      this.findData = {
        invoice_type: this.invoice_type,
        template_type: this.arrayTemplate,
        is_multi_vat: this.is_multi_vat,
        language: this.language,
        paper_size: this.papersize,
        type_circulars: this.type_circulars
      };
      this.loadData(this.findData);
    }

  }
  typeCircularChange(e) {
    this.endLoad = false;
    this.offsetEntity = 4;
    this.type_circulars = e.value;
    this.findData = {
      invoice_type: this.invoice_type,
      template_type: this.arrayTemplate,
      is_multi_vat: this.is_multi_vat,
      language: this.language,
      paper_size: this.papersize,
      type_circulars: this.type_circulars
    };
    this.loadData(this.findData);
  }
  templateTypeChange(e) {
    this.endLoad = false;
    this.offsetEntity = 4;
    this.template_type = e.value;
    this.arrayTemplate = [];
    this.arrayTemplate.push(this.template_type);
    if (e.value == "HOA_DON_VE_MAY_BAY" || e.value == "HOA_DON_KIEM_TO_KHAI_HOAN_THUE") {
      this.vat_type = "MAU_NHIEU_LOAI_THUE";
      this.is_multi_vat = true;
      if (e.value == "HOA_DON_VE_MAY_BAY") {
        this.arrayTemplate.push("HOA_DON_VE_MAY_BAY_HOAN_TIEN");
        this.arrayTemplate.push("HOA_DON_VE_MAY_BAY_THU_TIEN");
        this.arrayTemplate.push("HOA_DON_VE_MAY_TONG_HOP");
        this.arrayTemplate.push("HOA_DON_VE_MAY_BAY_CHUNG");
        this.arrayTemplate.push("HOA_DON_VE_MAY_BAY_HOAN_VE");
      }
    }
    else {
      this.vat_type = "MAU_MOT_LOAI_THUE";
      this.is_multi_vat = false;
      if (e.value == "HOA_DON_KINH_DOANH_XE") {
        this.arrayTemplate.push("HOA_DON_BAN_GIAO_XE");
        this.arrayTemplate.push("HOA_DON_BAO_DUONG_XE");
      }
    }
    this.papersize = "A4_DOC";
    if (e.value != "MAU_CO_BAN") {
      if (e.value == "HOA_DON_XANG_DAU")
        this.subPaperSizes = this.papersizes.filter(p => p.code != "A4_NGANG");
      else
        this.subPaperSizes = this.papersizes;
    }
    else
      this.subPaperSizes = this.papersizes;
    this.setPaperSizeByTemplateType();
    this.setLanguageByTypeInvoice();
    if (e.value == sEnum.templatetype.PhieuInMayTinhTien || e.value == sEnum.templatetype.VeTheInSanMenhGia || e.value == sEnum.templatetype.VeTheKhongInSanMenhGia) {
      this.subPaperSizes = this.billPaperSizes;
      this.papersize = this.subPaperSizes[0].code;
    }
    this.findData = {
      invoice_type: this.invoice_type,
      template_type: this.arrayTemplate,
      is_multi_vat: this.is_multi_vat,
      language: this.language,
      paper_size: this.papersize,
      type_circulars: this.type_circulars
    };
    this.loadData(this.findData);
  }
  vatTypeChange(e) {
    this.endLoad = false;
    this.offsetEntity = 4;
    if (e.value == 'MAU_MOT_LOAI_THUE')
      this.is_multi_vat = false;
    else
      this.is_multi_vat = true;
    this.findData = {
      invoice_type: this.invoice_type,
      template_type: this.arrayTemplate,
      is_multi_vat: this.is_multi_vat,
      language: this.language,
      paper_size: this.papersize,
      type_circulars: this.type_circulars
    };
    this.loadData(this.findData);
  }
  languageChange(e) {
    this.endLoad = false;
    this.offsetEntity = 4;
    this.language = e.value;
    this.findData = {
      invoice_type: this.invoice_type,
      template_type: this.arrayTemplate,
      is_multi_vat: this.is_multi_vat,
      language: this.language,
      paper_size: this.papersize,
      type_circulars: this.type_circulars
    };
    this.loadData(this.findData);
  }
  papersizeChange(e) {
    this.endLoad = false;
    this.offsetEntity = 4;
    this.papersize = e.value;
    this.findData = {
      invoice_type: this.invoice_type,
      template_type: this.arrayTemplate,
      is_multi_vat: this.is_multi_vat,
      language: this.language,
      paper_size: this.papersize,
      type_circulars: this.type_circulars
    };
    this.loadData(this.findData);
  }
  ngOnDestroy() {
    $('.main').unbind('scroll');
  }
  _loadDataDefault() {
    /* Load loại hóa đơn */
    this.invoiceTypes = [
      {
        name: this._translate.THONG_TIN.hoa_don_gtgt,
        code: sEnum.loaihoadon.GTKT01
      },
      {
        name: this._translate.THONG_TIN.hoa_don_ban_hang,
        code: sEnum.loaihoadon.GTTT02
      },
      {
        name: this._translate.THONG_TIN.hoa_don_ban_hang_phi_thue_quan,
        code: sEnum.loaihoadon.KPTQ07
      },
      {
        name: this._translate.THONG_TIN.phieu_thu,
        code: sEnum.loaihoadon.HSPT
      },
      {
        name: this._translate.THONG_TIN.phieu_xuat_kho_dai_ly,
        code: sEnum.loaihoadon.HGDL04
      },
      {
        name: this._translate.THONG_TIN.phieu_xuat_kho_noi_bo,
        code: sEnum.loaihoadon.XKNB03
      },
      {
        name: this._translate.THONG_TIN.chung_tu_thue_tncn,
        code: sEnum.loaihoadon.CTT56
      },
      {
        name: this._translate.THONG_TIN.bien_lai,
        code: sEnum.loaihoadon.BLP
      },
      {
        name: this._translate.THONG_TIN.ve_gia_tri_gia_tang,
        code: sEnum.loaihoadon.VETHEGTKT01
      },
      {
        name: this._translate.THONG_TIN.ve_ban_hang,
        code: sEnum.loaihoadon.VETHEGTTT02
      },
    ];
    /* Loại hóa đơn biên lai điện tử */
    this.receiptTypeArray = [
      {
        name: this._translate.THONG_TIN.bien_lai_khong_in_san_menh_gia,
        code: sEnum.templatetype.BienLaiKhongInSanMenhGia
      },
      {
        name: this._translate.THONG_TIN.bien_lai_in_san_menh_gia,
        code: sEnum.templatetype.BienLaiInSanMenhGia
      },
    ];
    /* Loại vé thẻ điện tử */
    this.ds_loai_ve = [
      {
        name: this._translate.THONG_TIN.ve_khong_in_san_menh_gia,
        code: sEnum.templatetype.VeTheKhongInSanMenhGia
      },
      {
        name: this._translate.THONG_TIN.ve_in_san_menh_gia,
        code: sEnum.templatetype.VeTheInSanMenhGia
      },
    ];
    /* Loại hóa đơn mặc định load mẫu tiếng việt */
    this.vnArray = [sEnum.loaihoadon.HGDL04, sEnum.loaihoadon.XKNB03, sEnum.loaihoadon.HSPT, sEnum.loaihoadon.BLP, sEnum.loaihoadon.VETHEGTKT01, sEnum.loaihoadon.VETHEGTTT02];
    /* Load khổ giấy máy tính tiền */
    this.billPaperSizes = [
      {
        name: "Khổ 80mm",
        code: "A80_DOC"
      },
      {
        name: "Khổ 57mm",
        code: "A57_DOC"
      },
    ]
  }
  setLanguageByTypeInvoice() {
    this.language = "EN";
    if (this.vnArray.includes(this.invoice_type) || this.template_type == sEnum.templatetype.PhieuInMayTinhTien)
      this.language = "VI";
  }
  setPaperSizeByTemplateType() {
    switch (this.template_type) {
      case sEnum.templatetype.BienLaiInSanMenhGia:
      case sEnum.templatetype.BienLaiKhongInSanMenhGia:
        this.papersize = "A5_NGANG";
        break;
    }
  }

}
