  add() {
    this.router.navigate(['/system/template/init']);
  }
  addMTT() {
    Library.showloading();
    this.templateService._getDataToTemplate().subscribe(rs => {
      if (rs.status == 'success') {
        let infoDangKy = rs.data;
        const initialState = {
          invoice_releases: '',
          infoDangKy: infoDangKy
        };
        this.bsModalRef = this.modalService.show(BillCashRegisterComponent, {
          class: 'modal-lg', initialState, backdrop: 'static',
          keyboard: false
        });
        this.modalService.onHide.subscribe(() => {
          this.loadList();
        });
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      Library.hideloading();
    });

  }
  edit(data) {
    if (data.template_type === sEnum.templatetype.HoaDonMayTinhTien) {
      Library.showloading();
      this.templateService._getDataToTemplate().subscribe(rs => {
        if (rs.status == 'success') {
          let infoDangKy = rs.data;
          const initialState = {
            frmData: data,
            id: data.id,
            isEdit: true,
            invoice_releases: '',
            infoDangKy: infoDangKy
          };
          this.bsModalRef = this.modalService.show(BillCashRegisterComponent, {
            class: 'modal-lg', initialState, backdrop: 'static',
            keyboard: false
          });
          this.modalService.onHide.subscribe(() => {

          });
        } else {
          Library.notify(rs.message, 'error');
        }
      }, err => { }, () => {
        Library.hideloading();
      });
    } else {
      const tempId = data ? data.id : this.selectedRows[0];
      if (data.is_html_template) {
        this.notifyService.getRealeaseByTemplateId(tempId).subscribe(
          (res: any) => {
            if (res.status === 'success') {
              if (res.data.invoice_releases && res.data.invoice_releases.length) {
                let lstRelease = res.data.invoice_releases.filter(p => p.release_status == "DA_TB_THUE" || p.release_status == "DA_HUY_SO")
                if (lstRelease && lstRelease.length > 0)
                  this.templateService.istemplateOld = true;
              }
              this.router.navigate([`/system/template/new`], { queryParams: { id: tempId } });
            }
          }, error => Library.notify(error, 'error'),
          () => Library.hideloading()
        );

      } else {
        this.router.navigate([`/system/template/old`], { queryParams: { id: tempId } });
      }
    }

  }

  copy(data) {
    Library.showloading();
    const tempId = data ? data.id : this.selectedRows[0];
    this.templateService.show(tempId).subscribe(rs => {
      Library.hideloading();
      if (rs.status == "success") {
        let data = rs.data.template;
        this.templateService.invoice_type = data.invoice_type;
        this.templateService.data = data;
        this.templateService.isCopy = true;
        this.router.navigate(['system/template/new']);
      } else { // id not found
        this.router.navigate(['/system/template']);
        return null;
      }
    });
    // if (data.is_html_template) {
    //   this.router.navigate([`/system/template/new`], { queryParams: { id: tempId } });
    // } else {
    //   this.router.navigate([`/system/template/old`], { queryParams: { id: tempId } });
    // }
  }

  delete(data) {
    const ids = data ? data.id : this.selectedRows.join(',');
    var result = Library.confirm("Bạn có chắc chắn muốn xóa đối tượng đã chọn?", "Thông báo");
    result.then((dialogResult) => {
      if (dialogResult) {
        Library.showloading();
        this.templateService.delete(ids).subscribe(rs => {
          if (rs.status === "success") {
            Library.notify("Xóa dữ liệu thành công", 'success');
            this.loadList();
          } else {
            Library.notify(rs.message, 'error');
          }
        }, err => { }, () => {
          Library.hideloading();
        });
      }
    });
  }

  view(item) {
    let win = window.open("", "", "width=200, height=100");
    win.document.body.innerHTML = "Đang tải dữ liệu!";
    Library.showloading();
    this.templateService.view(item.id).subscribe(rs => {
      let str_html = "";
      if (rs[0].data) {
        str_html += `<a title="Download File" style="font-family: \'Verdana\';color: #333;font-weight: 600;float:right; margin-bottom: 5px;
      margin-right: 20px;" download="hoadonmau.png" href="data:image/png;base64,${rs[0].data.template.jpg_file}">Tải mẫu gốc</a>`;
      }
      if (rs[1].data) {
        str_html += `<a title="Download File" style="font-family: \'Verdana\';color: #333;font-weight: 600;float:right; margin-bottom: 5px;
        margin-right: 20px;" download="hoadonchuyendoi.png" href="data:image/png;base64,${rs[1].data.template.jpg_file}">Tải mẫu chuyển đổi</a>`;
      }
      if (rs[0].data) {
        str_html += `<div style="text-align: center;"><img src="data:image/png;base64,${rs[0].data.template.jpg_file}"></div>`;
      }
      if (rs[1].data) {
        str_html += `<div style="text-align: center; margin-top: 15px;"><img src="data:image/png;base64,${rs[1].data.template.jpg_file}"></div>`;
      }
      win.document.body.innerHTML = str_html;
      win.resizeTo(screen.availWidth, screen.availHeight);
    }, err => { }, () => {
      Library.hideloading();
    });
    // let win = window.open("", "Preview", "toolbar=no,location=no,directories=no,status=no,menubar=no,scrollbars=yes,resizable=yes");
    // win.document.body.innerHTML = `<div style="text-align: center;"><img src="data:image/png;base64,${item.html_file}"></div>`;
  }

