import { Component, OnInit, ViewChild, AfterViewInit, Input } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { DxFormComponent } from 'devextreme-angular';
import * as moment from 'moment';
import { Base64 } from 'js-base64';
declare var $: any;

import { ModifyModel, TemplateService } from '@modules/template/shared';
import { pattern } from '@config/index';
import { Library, AuthService, Helper } from '@core/index';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'template-modify',
  templateUrl: './modify.component.html',
  styleUrls: ['./modify.component.scss']
})
export class ModifyComponent implements OnInit, AfterViewInit {
  @ViewChild(DxFormComponent) form: DxFormComponent;

  id: string = "";
  frmData: ModifyModel = new ModifyModel();
  pattern: any = pattern;
  dataPreview: string;
  rules = { 'h': /^[A-Za-z]{2}[0-9]{2}\z/ };
  maskTemplateCode: string;
  maskInvoiceSeries: string = 'CC/00E';
  invoice_type: string;
  checkboxChuyenDoi: boolean = false;
  selector: any = {};
  isDisIsViewPagesXY: any = true;
  isDisXemChuyenDoi: any = true;
  watermark_text_directions = [
    {
      name: 'Ngang',
      value: 'Horizontal'
    },
    {
      name: 'Dọc',
      value: 'Vertical'
    },
    {
      name: 'Chéo',
      value: 'ForwardDiagonal'
    },
    {
      name: 'Chéo ngược',
      value: 'BackwardDiagonal'
    },
  ];
  constructor(
    private authService: AuthService,
    private route: ActivatedRoute,
    private router: Router,
    private templateService: TemplateService,
    public translate: TranslateService,
  ) {
    translate.use(this.authService.getLanguage());
  }

  ngOnInit() {
    this.id = this.route.snapshot.queryParams['id'];
    this.route.data.subscribe((data: any) => {
      if (this.isEdit()) {
        this.invoice_type = data.template.invoice_type;
        this.frmData.fillData(data.template, this.invoice_type, true, false);
        this.dataPreview = Base64.decode(data.template.html_file);
        if (data.template.json_data.watermark_text_color)
          this.frmData.json_data.watermark_text_color = this.rgbToHex(data.template.json_data.watermark_text_color);
        // Helper.dowloadFile('demo.html', 'html', data.template.html_file);
        if (data.template.is_multi_pages) {
          this.isDisIsViewPagesXY = false;
        } else {
          this.isDisIsViewPagesXY = true;
        }
        if (data.template.is_use_form_convert) {
          this.isDisXemChuyenDoi = false;
        } else {
          this.isDisXemChuyenDoi = true;
        }
      } else {
        //trường mở rộng
        this.isDisXemChuyenDoi = false;

        this.frmData.is_multi_vat = this.templateService.is_multi_vat;
        // this.frmData.is_multi_pages = false; //this.templateService.is_multi_pages
        this.isDisIsViewPagesXY = false;
        this.frmData.is_view_pages_xy = false;
        this.frmData.seller_sign_position = this.templateService.seller_sign_position;
        this.frmData.buyer_sign_position = this.templateService.buyer_sign_position;
        this.frmData.is_discount = this.templateService.is_discount;

        //fill
        let year = moment(new Date()).format("YYYY").slice(2, 4);
        this.frmData.invoice_series = `  ${year}`;
        let account = this.authService.getCurrentUser().account;
        this.invoice_type = this.templateService.invoice_type;
        this.frmData.json_data.name = account.name;
        this.frmData.json_data.tax_code = account.tax_code;
        this.frmData.json_data.business_address = account.business_address;
        this.frmData.json_data.company_email = account.email;
        this.frmData.json_data.phone = account.phone;
        this.frmData.json_data.bank_name = account.bank_name;
        this.frmData.json_data.bank_account_number = account.bank_account_number;
        this.frmData.json_data.website = account.website;

        //preview
        this.dataPreview = Base64.decode(this.templateService.html_file);
        // this.dataPreview = Base64.decode(this.pattern.demo);
      }
      this.maskTemplateCode = `\\${this.invoice_type}\\0/000`;



      this.genDatatoTemplate();
    });
  }
  rgbToHex(str) { //r, g, b
    let a = str.split(";");
    console.log(a);
    return "#" + this.componentToHex(a[0]) + this.componentToHex(a[1]) + this.componentToHex(a[2]);
  }
  componentToHex(c) {
    var hex = Number(c).toString(16);
    if (hex.length < 2) {
      hex = "0" + hex;
    }
    return hex;
  }
  genDatatoTemplate() {
    //preview
    let iframe: any = document.getElementById('preview-iframe');
    iframe.contentDocument.open("text/html", "replace");
    iframe.contentDocument.write(this.dataPreview);
    iframe.contentDocument.close();

    //set element
    let preview = $('#preview-iframe').contents();
    this.selector.invoice_series = preview.find('nobr:contains([KY_HIEU])');
    this.selector.template_code = preview.find('nobr:contains([MAU_SO])');
    this.selector.name = preview.find('nobr:contains([TEN_DV])');
    this.selector.tax_code = preview.find('nobr:contains([MST_DV])');
    // this.selector.business_address = preview.find('nobr:contains([DIA_CHI_DK])');
    this.selector.bank_account_number = preview.find('nobr:contains([TAI_KHOAN])');
    this.selector.phone = preview.find('nobr:contains([DIEN_THOAI])');
    this.selector.company_email = preview.find('nobr:contains([EMAIL])');
    this.selector.website = preview.find('nobr:contains([WEBSITE])');
    this.reloadIframePreview(1);
  }

  a = (e) => {
    console.log(e.value);
  }

  reloadIframePreview = (e) => {
    this.frmData.invoice_series = this.frmData.invoice_series.toUpperCase();
    let data = this.frmData;

    let a = data.invoice_series.substr(0, 2);
    let b = data.invoice_series.substr(2, 2);

    this.selector.template_code.html(`${this.invoice_type}0/` + data.template_code);
    this.selector.invoice_series.html(a + '/' + b + 'E');
    this.selector.name.html(data.json_data.name);
    this.selector.tax_code.html(data.json_data.tax_code);
    // this.selector.business_address.html(data.json_data.business_address);
    this.selector.phone.html(data.json_data.phone);
    let tai_khoan = "";
    if (data.json_data.bank_account_number) {
      tai_khoan = data.json_data.bank_account_number;
    }
    if (tai_khoan && data.json_data.bank_name) {
      tai_khoan += " - " + data.json_data.bank_name
    }
    else if (!tai_khoan && data.json_data.bank_name) {
      tai_khoan = data.json_data.bank_name
    }
    this.selector.bank_account_number.html(tai_khoan);


    this.selector.website.html(data.json_data.website);
    this.selector.company_email.html(data.json_data.company_email);
  }

  reloadFromServer = () => {
    let data = JSON.parse(JSON.stringify(this.frmData));
    let a = data.invoice_series.substr(0, 2);
    let b = data.invoice_series.substr(2, 2);
    let rgbColor = this.hexToRgb(data.json_data.watermark_text_color);
    if (rgbColor) {
      data.json_data.watermark_text_color = `${rgbColor.r};${rgbColor.g};${rgbColor.b}`;
    }
    data.invoice_series = a + '/' + b + 'E';
    data.template_code = `${this.invoice_type}0/` + data.template_code;
    if (this.isEdit()) {
      data.raw_file = this.frmData.raw_file;
      data.invoice_type = this.frmData.invoice_type;
    } else {
      data.raw_file = this.templateService.raw_file;
      data.invoice_type = this.templateService.invoice_type;
    }

    let temp = this.checkboxChuyenDoi ? 1 : 0;
    Library.showloading();
    this.templateService.getTemplateModify(data, temp).subscribe(rs => {
      if (rs.status == 'success') {
        this.dataPreview = Base64.decode(rs.data.template.html_file);
        // Helper.dowloadFile('demo.html', 'html', rs.data.template.html_file);
        this.genDatatoTemplate();
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      Library.hideloading();
    });
  }

  hexToRgb(hex) {
    var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
    return result ? {
      r: parseInt(result[1], 16),
      g: parseInt(result[2], 16),
      b: parseInt(result[3], 16)
    } : null;
  }

  xoaLogo() {
    this.frmData.extend_info.logo = 'R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw==';
    $('#logo-file').value = "";
    this.reloadFromServer();
    $('.logo-file-label').html('Chọn logo đơn vị');
  }

  xoaAnhNen() {
    this.frmData.extend_info.watermark_image = 'R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw==';
    $('#watermark-file').value = "";
    this.reloadFromServer();
    $('.watermark-file-label').html('Chọn ảnh nền đơn vị');
  }

  ngAfterViewInit() {
    this.form.instance.getEditor("template_code").focus();
  }

  isEdit() {
    if (!this.id) {
      return false;
    }
    return true;
  }

  logoChanged(e) {
    let fileReader = new FileReader();
    let pattern = /image-*/;
    if (!e.target.files[0].type.match(pattern)) {
      Library.notify('Định dạng file không đúng', 'error');
      return;
    } else {
      if (e.target.files[0].size <= 307200) {
        fileReader.readAsDataURL(e.target.files[0]);
        fileReader.onload = (evt) => {
          let base64result = fileReader.result.toString().split(',')[1];
          this.frmData.extend_info.logo = base64result;
          $('.logo-file-label').html(e.target.files[0].name);
          this.reloadFromServer();
        }
      } else {
        Library.notify('Chỉ nên upload ảnh kích cỡ nhỏ hơn 300KB', 'error');
      }
    }
  }

  watermarkChanged(e) {
    let fileReader = new FileReader();
    let pattern = /image-*/;
    if (!e.target.files[0].type.match(pattern)) {
      Library.notify('Định dạng file không đúng', 'error');
      return;
    } else {
      if (e.target.files[0].size <= 1048576) {
        fileReader.readAsDataURL(e.target.files[0]);
        fileReader.onload = (evt) => {
          let base64result = fileReader.result.toString().split(',')[1];
          this.frmData.extend_info.watermark_image = base64result;
          $('.watermark-file-label').html(e.target.files[0].name);
          this.reloadFromServer();
        }
      } else {
        Library.notify('Chỉ nên upload ảnh kích cỡ nhỏ hơn 1MB', 'error');
      }
    }
  }

  onIsMultiPages = (e) => {
    this.reloadFromServer();
    if (e.value) {
      this.isDisIsViewPagesXY = false;
    } else {
      this.isDisIsViewPagesXY = true;
    }
  }

  onIsUseFormConvert = (e) => {
    if (e.value) {
      this.isDisXemChuyenDoi = false;
    } else {
      this.isDisXemChuyenDoi = true;
      this.checkboxChuyenDoi = false;
    }
  }

  onSubmit(e) {
    let data = JSON.parse(JSON.stringify(this.frmData));
    let a = data.invoice_series.substr(0, 2);
    let b = data.invoice_series.substr(2, 2);

    let rgbColor = this.hexToRgb(data.json_data.watermark_text_color);
    if (rgbColor) {
      data.json_data.watermark_text_color = `${rgbColor.r};${rgbColor.g};${rgbColor.b}`;
    }
    data.is_html_template = false;
    data.invoice_series = a + '/' + b + 'E';
    data.template_code = `${this.invoice_type}0/` + data.template_code;
    console.log(data);

    if (this.isEdit()) {
      this.edit(data);
    } else {
      this.add(data);
    }
  }

  add(data) {
    Library.showloading();
    data.invoice_type = this.templateService.invoice_type;
    data.raw_file = this.templateService.raw_file;
    this.templateService.add(data).subscribe(rs => {
      if (rs.status == 'success') {
        Library.notify('Thêm mới thành công', 'success');
        this.router.navigate(['/system/template']);
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      Library.hideloading();
    });
  }

  edit(data) {
    Library.showloading();
    this.templateService.modify(this.id, data).subscribe(rs => {
      if (rs.status == 'success') {
        Library.notify('Sửa mẫu thành công', 'success');
        // this.dataPreview = rs.data.template.html_file;
        this.router.navigate(['/system/template']);
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      Library.hideloading();
    });
  }

  checkboxChange() {
    this.reloadFromServer();
  }

}
