import { Component, OnInit } from '@angular/core';
import { AuthService } from '@core/services';
import { TranslateService } from '@ngx-translate/core';
import { BsModalRef } from 'ngx-bootstrap';
import { ModifyModel, TemplateService } from '@modules/template/shared';
import { isBuffer } from 'util';
import * as moment from 'moment';
import { Library, Helper, VStringService, sEnum } from '@core/index';
import { Router } from '@angular/router';

@Component({
  selector: 'app-bill-cash-register',
  templateUrl: './bill-cash-register.component.html',
  styleUrls: ['./bill-cash-register.component.scss']
})
export class BillCashRegisterComponent implements OnInit {
  frmData: ModifyModel = new ModifyModel();
  _language: any;
  _translate: any;
  isDisplayKyHieu: boolean = false;
  isDisplayMauSo: boolean = false;
  formatMaskSeries: any;
  maskRules: any;
  typeinvoices = [];
  infoDangKy: any;
  localStorage: any;
  isEdit: boolean = false;
  HinhThucHD: any;
  id: any;
  currentUser: any;
  namLapHD: any;
  constructor(private authService: AuthService,
    public translate: TranslateService,
    private templateService: TemplateService,
    private router: Router,
    public bsModalRef: BsModalRef,) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    translate.use(this._language);
    this.currentUser = this.authService.getCurrentUser();
  }

  ngOnInit() {
    this.typeinvoices =
      [
        { name: 'Hóa đơn GTGT', code: '01GTKT' },
        { name: 'Hóa đơn bán hàng ', code: '02GTTT' },
      ];

    this.formatMaskSeries = "TT";
    this.maskRules =
    {
      // "C": /[c|C|k|K]/,
      // "S": /[0-9]/,
      // "L": /[t|T|d|D|l|L|m|M|n|N|b|B|g|G|h|H]/,
      "T": /[A-Za-z]/
    }
    if (this.isEdit) {

    }
    else {
      const localData = localStorage.getItem('infoRegister')
      this.frmData.template_type = sEnum.templatetype.HoaDonMayTinhTien;
      this.frmData.is_decree_new = true;
      this.frmData.invoice_type = "01GTKT";
      this.frmData.template_code = "1";
      this.frmData.json_data.business_address = this.currentUser.account.business_address
      this.frmData.json_data.name = this.currentUser.account.name;
      this.HinhThucHD = "C";
      // this.frmData.json_data.business_address = localData;
      // this.frmData.json_data.name = ;
      if (this.infoDangKy.is_invoice_with_code == "0") {
        this.frmData.is_invoice_with_code = false;
      }
      else if (this.infoDangKy.is_invoice_with_code == "1") {
        this.frmData.is_invoice_with_code = true;
      }
      this.namLapHD = moment(new Date()).format("YYYY").slice(2, 4);
      let loaiHD = "M";
      // this.frmData.invoice_series = this.HinhThucHD + namLapHD + loaiHD;
      this.frmData["series_short"] = this.HinhThucHD + this.namLapHD + loaiHD;
      this.frmData["series_short2"] = ''
    }
  }
  onFieldDataChanged() {
    let ts = "";
  }
  dong() {
    this.bsModalRef.hide();
    // this.router.navigate(['/system/template']);
  }
  ghi() {
    setTimeout(() => {
      let data = JSON.parse(JSON.stringify(this.frmData));
      if (this.isEdit) {
        this.editTT78(data);
      } else {
        this.addTT78(data);
      }
    }, 300);
  }
  editTT78(data) {
    Library.showloading();
    delete data.invoice_releases;
    delete data.series_short;
    delete data.series_short2;
    this.templateService.modifyNew(this.id, data).subscribe(rs => {
      if (rs.status == 'success') {
        Library.notify('Sửa mẫu thành công', 'success');
        this.dong();
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      Library.hideloading();
    });
  }
  addTT78(data) {
    Library.showloading();
    delete data.series_short;
    delete data.series_short2;
    data.invoice_type = this.frmData.invoice_type;
    //   data.raw_file = this.templateService.raw_file;
    this.templateService.addTempt(data).subscribe(rs => {
      if (rs.status == 'success') {
        Library.notify('Thêm mới thành công', 'success');
        this.id = rs.data.template.id;
        this.frmData.invoice_type = rs.data.template.invoice_type;
        this.dong();
        //    this.router.navigate(['/system/template']);
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      Library.hideloading();
    });
  }
  onClickMauSo() {
    this.isDisplayMauSo = !this.isDisplayMauSo;
  }
  onClickKyHieu() {
    this.isDisplayKyHieu = !this.isDisplayKyHieu;
  }
  changedTypeInvoice(data) {
    this.frmData.invoice_type = data.itemData.code;
    if (data.itemData.code == "01GTKT") {
      this.frmData.template_code = "1";
    }
    else if (data.itemData.code == "02GTTT") {
      this.frmData.template_code = "2";
    }
  }
  onChangeSeries = (e) => {
    e.value.toUpperCase();
    this.frmData["series_short2"] = e.value.toUpperCase();
    this.frmData.invoice_series = this.HinhThucHD + this.namLapHD + "M" + e.value.toUpperCase();
    // let ts = this.frmData.invoice_series;
  };
}
